import AccessButton from "../components/AccessButton";
import LayoutDefault from "../layouts/LayoutDefault";
import { useTranslation } from "react-i18next";

function HomePage() {
  const { t } = useTranslation();
  return (
    <LayoutDefault className="">
      <div className="flex-1 w-full">
        <section className="bg-white px-4 py-4 shadow rounded">
          <h1 className="text-2xl font-semibold border-b pb-1 mb-4 ">
            {t("home.welcome")}
          </h1>
        </section>
        <section className="w-4/6 min-h-96 flex flex-col justify-around items-center mx-auto">
          <AccessButton
            title={"Productos"}
            name={"Crear, Modificar productos existente"}
          />
        </section>
      </div>
    </LayoutDefault>
  );
}

export default HomePage;
