import PropTypes from "prop-types";
const AccessButton = ({
  title = "Title Name app",
  name = "Name Access App",
  src = "https://via.placeholder.com/150",
}) => {
  return (
    <article className="grid grid-cols-1 grid-rows-6 place-content-between place-items-center bg-white rounded shadow max-w-48 max-h-52 p-4 cursor-pointer relative overflow-hidden group active:scale-95 hover:scale-105 transform transition-transform duration-300">
      <div className="row-span-3">
        <img
          src={src}
          className="object-cover object-center"
          width={64}
          height={64}
        />
      </div>
      <div className="row-span-2 ">
        <p className="text-md font-semibold text-center text-wrap truncate w-40 h-5">
          {title}
        </p>
      </div>
      <div className="row-span-1">
        <p className="text-xs font-semibold text-slate-400 text-center capitalize">
          {name}
        </p>
      </div>
      <div
        className={`absolute top-0 -inset-full h-full w-1/2 z-5 block transform -skew-x-12 bg-gradient-to-r from-transparent to-sky-400/50 opacity-40 group-hover:animate-shine`}
      />
    </article>
  );
};

AccessButton.propTypes = {
  title: PropTypes.string,
  name: PropTypes.string,
  src: PropTypes.string,
};

export default AccessButton;
